.app{
    margin: 0;
}

.banner-section{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    margin: 50px;
}

.banner{
    display: grid;
    grid-template-columns: 0.5fr 1fr;
    justify-items: end;
    align-items: center;
    background-image: url("/public/img/banner.jpeg");
    background-size:cover;
    background-position: center;
    padding-top: 40px;
    padding-bottom: 40px;
    border-radius: 30px;
}

.text-banner{
    color: black;
    font-size: 40px;
    font-weight: bold;
}

.text-banner span{
    color: #001AFF;
}

.banner-btn{
    background-color: #001AFF;
    color: white;
    margin-top: 20px;
    padding: 20px;
    padding-left: 25px;
    padding-right: 25px;
    font-size: 20px;
    font-weight: bold;
    border-radius: 20px;
    border: none;
    cursor: pointer;
    transition: box-shadow 0.2s ease-out;
}

.banner-btn:hover{
    box-shadow: 5px 5px 5px 0px black;
}

.banner-btn:active{
    background-color: white;
    color: #001AFF;
}



.products-section{
    max-width: 1200px;
    height: 1200px;
    margin: 0 auto;
}

.products-section h2{
    text-align: center;
}

.products{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

.product-img{
    justify-self: center;
    width: 200px;
}

.product-title{
    font-size: 20px;
    font-weight: bold;

}

.price{
    color: #9D9D9D;
    font-weight: bold;
}

.product-item{
    display: grid;
    border: 1px solid #9F9F9F;
    border-radius: 35px;
    padding: 20px;
    margin: 30px;
    transition: box-shadow 0.4s ease-out;
}

.product-item:hover{
    box-shadow: 0px 0px 10px 0px #616161;
}

.product-item p{
    margin-bottom: 5px;
    margin-top: 5px;
}

.product-price{
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.product-price span{
    font-weight: bold;
    font-size: 25px;
}

.add-cart{
    justify-self: end;
    background-color: white;
    border: 1px solid #9F9F9F;
    border-radius: 10px;
    padding: 7px;
    padding-bottom: 4px;
    cursor: pointer;
}
.add-cartcheck{
    justify-self: end;
    background-color: white;
    border: 1px solid #be2929;
    border-radius: 20px;
    padding: 7px;
    padding-bottom: 4px;
    cursor: pointer;
}
.add-cartcheck img{
    width: 20px;
}


.add-cart img{
    width: 15px;
}


/* Поиск */

.search{
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: center;
}

.search h2 {
    justify-self: start;
    font-size: 32px;
}

.search-block{
    justify-self: end;
    border: 1px solid #9f9f9f;
    border-radius: 10px;
    padding: 15px;
    width: 250px;
}

.search-block:hover{
    border: 1px solid black;
}

.search-block img{
    width: 13px;
    margin-right: 15px;
}

.search-block input {
    border: none;
    font-size: 16px;
}

.search-block input:focus-visible {
    outline: none;
}

/* Кнопка Добавить в избранное */

.favorite-btn{
    border: 0;
    border-radius: 10px;
    background-color: #001aff;
    color: white;
    padding: 7px;
    font-size: 14px;
    text-align: start;
    width: fit-content;
    cursor: pointer;
    transition: transform 0.5s ease-in-out;
}

.favorite-btn:hover{
    transform: translateX(20px);
}
.favorite-btn_clicked{
    background-color: red;
    border: 0;
    border-radius: 10px;
    color: white;
    padding: 7px;
    font-size: 14px;
    text-align: start;
    width: fit-content;
    cursor: pointer;
    transition: transform 0.5s ease-in-out;

}